<template>
	<div id="single-blog">
		<h1>{{ blog.title }}</h1>
		<article>{{ blog.content }}</article>
		<p>Author: {{ blog.author }}</p>
		<ul>
			<li v-for="category in blog.categories">{{ category }}</li>
		</ul>
	</div>
</template>

<script>
export default {
	data() {
		return {
			// id: null,
			id: this.$route.params.id,
			blog: {}
		}
	},
	created() {
		this.$http.get('https://nn-vue-playlist-547b3.firebaseio.com/posts/' + this.id + '.json')
			.then(function(data){
				return data.json();
			})
			.then(function(data){
				this.blog = data;
			});
	}
}
</script>



